@font-face {
  font-family: "robotoblack";
  src: url("../../assets/fonts/roboto-black-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-black-webfont.woff") format("woff"), url("../../assets/fonts/roboto-black-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "robotobold";
  src: url("../../assets/fonts/roboto-bold-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-bold-webfont.woff") format("woff"), url("../../assets/fonts/roboto-bold-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "robotoregular";
  src: url("../../assets/fonts/roboto-regular-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-regular-webfont.woff") format("woff"), url("../../assets/fonts/roboto-regular-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
* {
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 10px;
  font-family: "robotoregular";
}

.header {
  height: 136px;
  width: 1336px;
  background-color: #000;
}

.container {
  width: 100%;
  max-width: 136.6rem;
  margin: 0 auto;
}

body {
  background: #fff;
}

h1 {
  font-size: 3rem;
  color: #000;
  font-family: "robotobold";
}
@media (min-width: 767px) {
  h1 {
    font-size: 4.8rem;
  }
}

p {
  font-family: "robotoregular";
}